import SqSingleCoupon from '@/components/sq-single-coupon/sq-single-coupon.vue'
export default {
	components: {
		SqSingleCoupon
	},
	data() {
		return {
			searchFixed: false,
			showAddMpToList: true,
			couponList: [],
			hotList: []
		};
	},
	onLoad() {
		this.getHotWord();
		this.getCouponList();
	},
	methods: {
		/**
		 * 点击搜索框
		 */
		clickSearchArea() {

		},
		/**
		 * 点击搜索词
		 */
		clickSearchItem() {

		},
		/**
		 * 获取 搜索发现热词
		 */
		getHotWord() {
			let options = {
				url: uni.apis.microHotWord,
				data: {}
			}
			uni.requestSqkb(options).then(res => {
				if (res.status_code == 1) {
					let hotList = res.data || [];
					this.hotList = hotList;
				} else {
					uni.showToast({
						title: res.message
					})
				}
			}).catch(e => {
				console.log(e);
				uni.showToast({
					title: e.message
				})
			})
		},
		/**
		 * 获取推荐商品列表
		 */
		getCouponList() {
			let options = {
				url: uni.apis.microRecommend,
				data: {},
				method: "POST"
			}
			uni.requestSqkb(options).then(res => {
				if (res.status_code == 1) {
					let couponList = res.data.coupon_list || [];
					this.couponList = couponList;
				} else {
					uni.showToast({
						title: res.meassge
					})
				}
			}).catch(e => {
				console.log(e);
				uni.showToast({
					title: e.message
				})
			})
		},
	}
}
